import Vue from 'vue'
import Vuex from 'vuex'
import { mutations, getter, actions } from './src/index.d'

Vue.use(Vuex)

const state = {
  busList: [],
  routerMode: 'page-go', // 制作前进后退动画
  currentSite: {}
}

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state,
  getter,
  mutations,
  actions
})

export default store
