import { GET_BUS_LIST, SET_ROUTER_MODE, SET_CURRENT_SITE } from '../Types'

export const mutations = {
  [GET_BUS_LIST] (state, list) {
    state.busList = list
  },
  [SET_ROUTER_MODE] (state, mode) {
    state.routerMode = mode
  },
  [SET_CURRENT_SITE] (state, site) {
    state.currentSite = site
  }
}
