import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const client = [
  {
    path: '/', // client -> xxx
    name: 'search',
    component: () => import('@/view/search/index')
  },
  {
    path: '/line', // client -> xxx
    name: 'line',
    component: () => import('@/view/line-map/index')
  }
]

const routes = [
  {
    path: '',
    redirect: 'client'
  },
  {
    path: '/client',
    component: () => import('@/Layout/client'),
    children: client
  },
  {
    path: '/server',
    component: () => import('@/Layout/server')
  },
]

const router = new VueRouter({
  routes
})

export default router
