<template>
  <div :class="className">
    <slot />
  </div>
</template>

<script>
  export default {
    name: 'tag',
    props: {
      type: {
        type: String,
        default: 'primary'
      }
    },
    computed: {
      className() {
        return ['v-easy-tag', 'v-easy-tag--' + this.type]
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~@/styles/src/colors";

  .v-easy-tag {
    display: inline-block;
    border-radius: 2px;
    padding: 4px 8px;
    vertical-align: middle;
    &--primary {
      color: white;
      background-color: @primary;
    }
  }
</style>
